<template>
  <div>
    <!-- 数据列表 - 筛选-->
    <el-card class="operate-container mt10" shadow="never">
      <el-tabs v-model="searchList.tabType" @tab-click="getData">
        <el-tab-pane label="全部" name="11"></el-tab-pane>
        <el-tab-pane label="进行中" name="0"></el-tab-pane>
        <el-tab-pane label="待结算" name="1"></el-tab-pane>
        <el-tab-pane label="退款" name="10"></el-tab-pane>
        <el-tab-pane label="交易成功" name="2"></el-tab-pane>
        <el-tab-pane label="交易关闭（失败）" name="3"></el-tab-pane>
      </el-tabs>
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="业务单号：">
            <el-input style="width: 203px" v-model="searchList.businessNumber" clearable @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="对方名称：">
            <el-input style="width: 203px" v-model="searchList.toUser" clearable @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="交易分类：">
            <el-select v-model="searchList.tradeType" clearable>
              <el-option label="订单入账" value="1"></el-option>
              <el-option label="退款" value="24"></el-option>
              <el-option label="交易手续费" value="21"></el-option>
              <el-option label="交易手续费退回" value="2"></el-option>
              <el-option label="佣金" value="22"></el-option>
              <el-option label="佣金退回" value="4"></el-option>
              <el-option label="邀请奖励" value="26"></el-option>
              <el-option label="邀请奖励退回" value="5"></el-option>
              <el-option label="提现" value="25"></el-option>
              <el-option label="提现手续费" value="23"></el-option>
              <el-option label="充值" value="3"></el-option>
              <el-option label="会员卡收入" value="6"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="付款时间：">
            <el-date-picker v-model="searchList.time"
              type="daterange"
              value-format="yyyy-MM-dd"
              :picker-options="pickerOptions"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="支付单号/流水号：">
            <el-input style="width: 203px" v-model="searchList.wxNumber" clearable @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="收支方向：">
            <el-select v-model="searchList.amountType" clearable>
              <el-option label="收入" value="1"></el-option>
              <el-option label="支出" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="支付模式：">
            <el-select v-model="searchList.payType" clearable>
              <el-option label="小程序支付" value="2"></el-option>
              <el-option label="微信支付-自有" value="1"></el-option>
              <el-option label="微信支付-代销" value="0"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card style="margin-top:20px;" class="filter-container" shadow="never">
      <el-table ref="multipleTable" :data="list" tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="createTime" label="时间" width="200"></el-table-column>
        <el-table-column prop="description" label="名称" width="400">
          <template slot-scope="scope">
            <div class="flex-row-center">
              <div>{{scope.row.description}}</div>
              <el-tag v-if="scope.row.payType == 0" style="margin-left:4px;" type="warning">代销</el-tag>
              <el-tag v-if="scope.row.payType == 1 || scope.row.payType == 2" style="margin-left:4px;">自有</el-tag>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="businessNumber" width="200">
          <template slot="header" slot-scope="scope">
            业务单编号
            <el-tooltip :data-type="scope.row" class="item" effect="dark" content="业务单号，指的是不同的业务的单号，例如：订单是订单号，提现是提现单号，分润是分润单号。关联单号，是指相关的订单号" placement="top">
              <i class="el-icon-question"></i>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="支付单号/流水号" width="300">
          <template slot-scope="scope">
            <div>{{scope.row.wxNumber ? scope.row.wxNumber : '-'}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="toUser" label="对方名称" width="260"></el-table-column>
        <el-table-column label="金额/明细" width="200">
          <template slot-scope="scope">
            <div class="flex-row-center" v-if="scope.row.settleState == 0">
              <div class="infoFont">￥{{scope.row.amount > 0 ? '+'+scope.row.amount : scope.row.amount}}</div>
              <el-tag style="margin-left:4px;" type="danger">未入账</el-tag>
            </div>
            <div class="flex-row-center" v-else-if="scope.row.settleState == 1">
              <div class="infoFont">￥{{scope.row.amount > 0 ? '+'+scope.row.amount : scope.row.amount}}</div>
              <el-tag style="margin-left:4px;" type="danger">未入账</el-tag>
            </div>
            <div class="flex-row-center" v-else>
              <div class="succeseFont">￥{{scope.row.amount > 0 ? '+'+scope.row.amount : scope.row.amount}}</div>
              <i 
                v-if="scope.row.tradeType != 3 && scope.row.tradeType != 25 && scope.row.tradeType != 6" 
                @click="goToMybill(scope.row)"
                style="font-size:20px;" 
                class="warningFont el-icon-caret-bottom curP"
              ></i>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="settleStateName" label="状态" width="200">
          <!-- <template slot-scope="scope">
            <div v-if="scope.row.settleState == 0">进行中</div>
            <div v-if="scope.row.settleState == 1">待结算</div>
            <div v-if="scope.row.settleState == 2">交易成功</div>
            <div v-if="scope.row.settleState == 3">交易关闭（交易失败）</div>
          </template> -->
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="goToInfo(scope.row)">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-end" style="padding-top:20px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5,10,15,20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopAssetRecord } from '@/api/assetsManage/account'
export default {
  props: ["getPost"],
  watch:{
    getPost(newvalue,oldValue){
      if(newvalue){
        console.log('请求了全部')
        this.getData()
      }
    },
  },
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1,
        time:[],
        tabType:'11'
      },
      pickerOptions: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime()- 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      list:[],
      listTotal:0
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
  },
  methods:{
    getData(){
      this.searchList.settleState = this.searchList.tabType == 11 ? '' : this.searchList.tabType
      this.searchList.startDate = this.searchList.time[0] || ''
      this.searchList.endDate = this.searchList.time[1] || ''
      console.log('全部请求的数据',this.searchList)
      getShopAssetRecord(this.searchList).then(res=>{
        console.log('全部交易记录列表',res)
        if(res.code ==200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    reset(){
      this.searchList.businessNumber = ''
      this.searchList.toUser = ''
      this.searchList.time = []
      this.searchList.tradeType = ''
      this.searchList.wxNumber = ''
      this.searchList.amountType = ''
      this.searchList.payType = ''
      this.getData()
    },
    goToInfo(e){
      if(e.tradeType == 3){
        console.log('充值')
        this.$emit('changeActive','1');
        return;
      }
      if(e.tradeType == 25){
        console.log('提现')
        this.$router.push({
          path:'/assetsManage/account/cashoutDetail',
          query:{
            id:e.id
          }
        })
        return;
      }
      if(e.tradeType == 6){
        console.log('会员卡开通记录')
        this.$router.push({
          path:'/memberManage/memberCenter',
          query:{
            type:1
          }
        })
        return;
      }
      this.$router.push({
        path:'/order/orderList/orderinfo',
        query:{
          orderId:e.orderId
        }
      })
    },
    goToMybill(e){
      this.$router.push({
        path:'/assetsManage/myBill',
        query:{
          orderId:e.orderId,
          afterSaleId:e.afterSaleId
        }
      })
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>